.certificates-container .certificates__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #4b4b4b;
    padding-bottom: 12px;
}

.certificates-container .certificates__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 12px;
    padding-right: 95px;
}

.certificates-container .certificates__content > *:not(:first-child) {
    margin-top: 17px;
}